        title: "24. Mike's Wedding Vows",
        answer: '1946',
        location: 'Dining Room',
        puzzleText: `<p>Read Mike's Wedding Vows to Brenna</p><p>"Words can't begin to describe the love I have for you". I think much of the time this kind of sentiment is used, people mean it as a placeholder to get across how BIG the love they have for someone is, that it's too immense in scale to wrap their head around let alone convey it through words. But I've been thinking about the kind of small love in a relationship, that isn't shown with extravagant proclamations or acts of self-sacrifice. I've been thinking about the kind of love and kinship that we sometimes save for pets, that seems to fall outside of the bounds of human connection. Silent trustful understanding day in and out that the person you are with cares for you, loves you, wants to be close, wants to feel your warmth and that more often than not, that the place where they want to be and the place where they are happen to be the same.</p><p>I commit to Brenna to be a witness to her life, to see her be herself and to love her as she is now and to love who she will become. I commit to being her companion and her memory bank, to help remind her of the places we've been and the friendships we've made, to help her remember her achievements and help her see herself the way I see her. I commit to being her stupid little guy, to trying to catch her gaze long enough with a dance or a joke good enough to keep her attention. To distract her from the work she intends to do so that I can get my fill of the feeling that comes from the corners of her mouth turning up against her will, while she shakes her head in disbelief at what I'll do to make her laugh.</p><p>Brenna already knows this, but we didn't really want to write vows to each other. It was something that was partially imposed upon us to prove the truth of our love as a declaration to others. I said it to her in private, and I'll say it here for the people in our lives to know. That words are just words. That my handle over them can't convey how much life we share with each other, that your presence is so large in my experience of this one life that I have, that I can't even take it all in at once anymore when I try. They can't compare to how it feels when you look at me and smile, the feeling of belonging and security in my heart and my head that I feel at the end of each night while we unpack our day and talk about what we're gonna do tomorrow. None of it can get across the decade we've spent with each other, and how somehow, during that time, we've only grown closer. There's a short story called "On Exactitude in Science" in which the cartographers of an empire seek to make a map of perfect accuracy, and so they go about this task by recreating the details of the empire point for point in perfect scale and recreation, until the map takes up the full size of the empire itself. To convey to others what this life we've built with each other is like, why we understand each other's wants and our faults, our motives and our fears, why we can see in each other who we were and who we are, it would just be words. It wouldn't do, and it would take too long. So I'll make it small; I love you and I feel loved by you. Let's keep going until our brains turn to mush and bones turn to dust.</p><p>Puzzle #24:</br>>What year was the short story referenced published?</p>`,
        hintOne: "The story is called 'On Exactitude in Science'",
        hintTwo: "I love my wife and I'm not afraid to say it.",
